<template>
 <div id="failureTab">
   <ul>
        <li 
        v-for="(item,index) in tabs" 
        :class="{active:index == num}"
         @click="tab(index)">{{item}}</li>
    </ul>
    <div class="tabCon">
        <div 
        v-for='(itemCon,index) in tabContents' 
        v-show=" index == num">{{itemCon}}</div>
    </div>
 </div>
</template>

<script>
import Vue from 'vue'
export default {
  name: 'HelloWorld',
  data () {
    return {
        tabs: ["Hourly", "Daily","Weekly","Monthly"],
        tabContents: ["小时", "天数","周期","月份"],
        num: 0
    }
  },                             
   mounted(){
    this.tab(this.num)
  },
   methods: {
     tab(index) {
            this.num = index;
        }
   },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#failureTab{
  position: absolute;
  top: 0.24rem;
  left: 1.2rem;
}
#failureTab ul{
  overflow: hidden;
  margin-bottom: 0.1rem;
}
#failureTab li{
 float: left;
 color: rgba(255,255,255,0.6);
 cursor: pointer;
 margin-left: 0.1rem;
}
#failureTab li.active{
 color: rgba(255,255,255,1);
}
</style>



