<template>
  <div class="car-status">
    <p>转向架-023 </p>
    <p>转向架故障分析 </p>
    <span class="rotate"></span>
    <div class="carGraph" id="WebGL-output"></div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.car-status{
  color: #fff;
  text-align: left;
  padding-left: 0.3rem;
  padding-top: 0.1rem;
  position: relative;
  width: 100%;
  height: 100%;
}
.carGraph{
  width: 80%;
  height: 80%;
}
.car-status p:nth-child(1){
  font-size: 0.18rem;
  margin-bottom: 0.05rem;
}
.car-status p:nth-child(2){
  font-size: 0.14rem;
 color: rgba(255,255,255,0.6);
}
.car-status .rotate{
  width: 0.41rem;
  height: 0.3rem;
  display: block;
  background: url('../../assets/control.png') no-repeat;
  background-size: 100% 100%;
  position: absolute;
  bottom:40%;
  right: 26%;
  z-index: 9999;
  cursor: pointer;
}
@media screen and (min-width:1401px) and (max-width:1440px) {
  .car-status .rotate{
    bottom:50%;
  }
}
@media screen and (min-width:1367px) and (max-width:1400px) {
  .car-status .rotate{
    bottom:56%;
    right: 30%;
  }
}
@media screen and (min-width:1360px) and (max-width:1366px) {
  .car-status .rotate{
    bottom:60%;
    right: 32%;
  }
}
</style>



