<template>
  <div class="deviceStatus">
    <device-pie />
    <DeviceTable />
  </div>
</template>

<script>
import DevicePie from './DevicePie'
import DeviceTable from './DeviceTable'
export default {
  name: 'Device',
  components: {
    DevicePie,
    DeviceTable,
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.deviceStatus{
  position: absolute;
  right: 0.1rem;
  top: 3.4rem;
 border: 1px solid;
 border-color: rgba(78, 85, 100,0.3);
}
</style>



