<template>
  <div class="failureAnalysis-graph">
    <div class="analysisHead">
      <p>故障分析</p>
      <p>...</p>
    </div>
    <FailTab />
    <FailCharts />
  </div>
</template>

<script>
import FailTab from './FailTab'
import FailCharts from './FailCharts'
export default {
  name: 'Device',
  components: {
    FailTab,
    FailCharts,
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.failureAnalysis-graph{
  border:1px solid;
 border-color: rgba(78, 85, 100,0.3);
  position: absolute;
  left: 0.1rem;
  top: 6.4rem;
  background: transparent;
}
.analysisHead{
  padding:0.2rem; 
  overflow: hidden;
}
.analysisHead>p{font-size: 0.18rem;}
.analysisHead>p:nth-child(1){
float: left;
}
.analysisHead>p:nth-child(2){
float: right;
font-weight: bold;
}
</style>



