<template>
  <div class="deviceStatus-graph">
    <div class="headBar">
      <h3>设备状态 </h3>
      <span>...</span>
    </div>
    <div class="chartsBox">
      <div class="chartsOne">
        <div id="chartsOne"></div>
        <p>Engine system</p>
      </div>
      <div class="chartsTwo">
        <div id="chartsTwo"></div>
        <p>drive system</p>
      </div>
      <div class="chartsThree">
        <div id="chartsThree"></div>
        <p>bodywork</p>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import echarts from 'echarts'
Vue.prototype.$echarts = echarts
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
   mounted(){
    this.drawOne();
    this.drawTwo();
    this.drawThree();
  },
   methods: {
    drawOne(){
        let myChartOne = this.$echarts.init(document.getElementById('chartsOne'))
        myChartOne.setOption( colorStyle(oneColor) )
    },
    drawTwo(){
        let myChartTwo = this.$echarts.init(document.getElementById('chartsTwo'))
        myChartTwo.setOption( colorStyle(twoColor) )
    },
    drawThree(){
        let myChartThree = this.$echarts.init(document.getElementById('chartsThree'))
        myChartThree.setOption( colorStyle(threeColor) )
    }
   },
}

let oneColor={
                    "color": "rgba(0,0,0,1)",
                    "borderColor": new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: 'rgba(178,187,118,0)'
                    }, {
                        offset: 1,
                        color: 'rgba(178,187,118,1)'
                    }]),
                    "borderWidth": 5
                }
let twoColor={
                    "color": "rgba(0,0,0,1)",
                    "borderColor": new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: 'rgba(116,188,121,0)'
                    }, {
                        offset: 1,
                        color: 'rgba(116,188,121,1)'
                    }]),
                    "borderWidth": 5
                }
 let threeColor={
                    "color": "rgba(0,0,0,1)",
                    "borderColor": new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: 'rgba(238,105,78,0)'
                    }, {
                        offset: 1,
                        color: 'rgba(238,105,78,1)'
                    }]),
                    "borderWidth": 5
                }
                             
  function colorStyle(styleColor){
    let option = {
    tooltip:{},
    "series": [
        {
            "center": [
                "50%",
                "50%"
            ],
            "radius": [
                "50%",
                "50%"
            ],
            "clockWise": false,
            "hoverAnimation": false,
            "type": "pie",
            radius: [35, 36],
            "data": [
                {
                    "value": 82.7,
                    label: {
                    normal: {
                        formatter: '{d}%',
                        position: 'center',
                        show: true,
                        textStyle: {
                            "fontSize": 12,
                            "fontWeight": "bold",
                            color:'#fff'
                        }
                    }
                },
                    "labelLine": {
                        "show": false
                    },
                     "itemStyle": {
                "normal": styleColor,
                "emphasis": {
                }
            },
                },
                {
                    "name": " ",
                    "value": 17.3,
                            "label": {
                                "show": false
                            },
                            "labelLine": {
                                "show": false
                            },
                    "itemStyle": {
                        "normal": {
                            "color": "rgba(0,0,0,0)",
                            "borderColor": "#5886f0",
                            "borderWidth": 0
                        },
                        "emphasis": {
                        }
                    }
                }
            ]
        },
    ]
  };
            return option;
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.chartsBox{
  overflow: hidden;
  border-top: 1px solid;
  border-bottom: 1px solid;
 border-color: rgba(78, 85, 100,0.4);
 padding-bottom: 0.1rem;
}
.headBar{
  overflow: hidden;
  height: 0.5rem;
  line-height: 0.5rem;
  padding:0 0.2rem; 
}
.headBar>h3{
  display: inline-block;
  font-size: 0.18rem;
}
.headBar>h3,
.chartsBox>div{
  float: left;
}
.headBar>span{
  float: right;
  font-size: 0.18rem;
font-weight: bold;
}
.chartsBox>div{
  width: 33%;
  text-align: center;
}
.chartsBox #chartsOne,
.chartsBox #chartsTwo,
.chartsBox #chartsThree{
  width: 1rem;
  height: 1rem;
  margin: 0 auto;
}
</style>



