<template>
  <div id="failureAnalysis">
  </div>
</template>

<script>
import Vue from 'vue'
import echarts from 'echarts'
Vue.prototype.$echarts = echarts
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
   mounted(){
    this.drawLine();
  },
   methods: {
    drawLine(){
       let myChart = this.$echarts.init(document.getElementById('failureAnalysis'))
       myChart.setOption({
            color: ['#ffc94c'],
            tooltip:{
              trigger: 'axis',
            },
        legend: {
          show: false,
        },
        grid: {
          left: 10,
          right: 10,
          top: 20,
          bottom: 20,
          containLabel: true
        },
        textStyle: {
          fontSize: '14',
          fontWeight: 'normal',
          color: 'rgba(255,255,255,0.6)'
        },
        xAxis: {
          show: true,
          type: 'category',
        axisTick: {
            show: false
        },
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(255, 255, 255, 0.1)',
            }
          },
          boundaryGap: false,
          data: ['01','02','03','04','05','06','07','08','09','10','11','12'],
        },
        yAxis: {
          show: true,
          type: 'value',
        axisTick: {
            show: false
        },
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(255, 255, 255, 0.1)',
            }
          },
          axisLabel: {
            show:false
          }
        },
        series: [{
        name: 'KCAL',
        type: 'line',
        showSymbol: false,
        areaStyle: {
            normal: {
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [{
                  offset: 0,
                  color: 'rgba(104, 225, 250, 1)',
                }, {
                  offset: 1,
                  color: 'rgba(115, 134, 197, 1)',
                }],
                globalCoord: false
              },
            }
          },
          markPoint: {
            data: [{
              type: 'max',
              label: {
                normal: {
                  show: true,
                  formatter: ''
                }
              },
              itemStyle: {
                normal: {
                  color: '#ff5555',
                  borderColor: 'rgba(255, 255, 255, 0.2)',
                  borderWidth: 12,
                }
              },
              symbol: 'circle',
              symbolSize: 12,
            }],
          },
        lineStyle: {
            normal: {
                width: 0
            }
        },
        data: [100, 200, 400, 250, 500, 275, 150, 450, 200, 180, 285, 100],
        smooth: true
    }, {
        name: 'POUNDS',
        type: 'line',
        showSymbol: false,
        areaStyle: {
            normal: {
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [{
                  offset: 0,
                  color: 'rgba(104, 225, 250, 1)',
                }, {
                  offset: 1,
                  color: 'rgba(115, 134, 197, 1)',
                }],
                globalCoord: false
              },
            }
          },
        lineStyle: {
            normal: {
                width: 0
            }
        },
        data: [120, 275, 500, 200, 450, 330, 180, 300, 360, 254, 425, 120],
        smooth: true
    }, {
        name: 'WORKOUTS',
        type: 'line',
        showSymbol: false,
        areaStyle: {
            normal: {
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [{
                  offset: 0,
                  color: 'rgba(104, 225, 250, 1)',
                }, {
                  offset: 1,
                  color: 'rgba(115, 134, 197, 1)',
                }],
                globalCoord: false
              },
            }
          },
        lineStyle: {
            normal: {
                width: 0
            }
        },
        data: [80, 175, 480, 200, 450, 300, 120, 290, 450, 300, 125, 100],
        smooth: true
    }]   
       })
    }
   },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#failureAnalysis{
  width: 14.2rem;
  height: 2.8rem;
  padding: 0 0.2rem;
}
</style>



